const app = getApp()
import Star from "@/components/uni-rate/uni-rate.vue"
export default {
	data() {
		return {
			tokey:"",
			imgList: [],
			topvalue:0,
			Couriervalue:0,
			Logisticsvalue:0,
			attitudevaluevalue:0,
			checkbox: [{
				value: 'A',
				checked: false
			}],
			bianhao:"",
			gid:"",
			img:"",
			goodname:"",
			yuming:"",
			neirongtext:""
		}
	},
	components:{
		Star
	},
	methods:{
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		//这是选中匿名评价
		CheckboxChange(e) {
			var items = this.checkbox,
				values = e.detail.value;
				if(items[0].checked){
					items[0].checked = false
				}else{
					items[0].checked = true
				}
		},
		// 这是星星顶部的子组件传过来的事件/
		change(e){
			this.topvalue = e.value
		},
		// 这是星星底部子组件穿过来的数据
		Couriertiselect(e){
			this.Couriervalue = e.value
		},
		Logisticsvalueselect(e){
			this.Logisticsvalue = e.value
		},
		attitudevaluevalueselect(e){
			this.attitudevaluevalue = e.value
		},
		//这选择相册中的图片然后删除
		DelImg(e) {
			uni.showModal({
				title: '召唤师',
				content: '确定要删除这段回忆吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		//将评价提交到后台
		submitevaluation(){
			let {topvalue,imgList,neirongtext} = this.$data
			if(topvalue==0 || imgList.length==0 || neirongtext==""){
				app.globalData.showtoastsame("请填写完整信息")
			}else{
				uni.request({
					url:`${app.globalData.Requestpath}order/orderGoodEvaluation`,
					method:"POST",
					data:{
						token:this.tokey,
						o_sn:this.bianhao,
						gid:this.gid,
						e_scores:topvalue,
						e_content:neirongtext,
						e_pic:imgList
					},
					success(res) {
						console.log(res)
						if(res.data.code==0){
							//请求成功跳到评价的页面
							uni.redirectTo({
								url:`/pages/evaluate/evaluate`
							})
						}else{
							app.globalData.Requestmethod(res.data.code,res.data.msg)
						}
					}
				})
			}
		}
	},
	onLoad(oipction) {
		const _this = this
		this.bianhao = atob(oipction.ordersnbianhao)//这是定单编号
		this.gid = atob(oipction.shopgooid)//这是商品id
		this.img = oipction.img
		this.goodname = oipction.goodname
		this.yuming = app.globalData.imgyuming
		uni.getStorage({
			key:"bindtokey",
			success(res){
				_this.tokey = res.data
				app.globalData.Detectionupdatetokey(res.data)
			}
		})
	}
}
